.head{background-color:#fff;position:fixed;width:100%;z-index:999}.head .logo{text-decoration:none;font-size:1.1rem;color:black}.head .customNav{display:none}.head .container{display:flex;justify-content:center}.head .fb{position:absolute;top:1.65rem;left:1.65rem;font-size:1.8rem;cursor:pointer}.head .fs{position:absolute;top:1.65rem;right:1.65rem;font-size:1.8rem;cursor:pointer;color:rgba(0,0,0,0.678)}.head #cartCounter{background-color:#fd2f2f;color:white;position:absolute;top:0.2rem;right:1rem;width:1.4rem;height:1.4rem;padding-top:0.05rem;border-radius:50%;text-align:center}.head ul{list-style-type:none;margin:0;padding:0;text-align:center;font-family:'Lalezar'}.head ul li{margin-bottom:12px}.head ul a{text-decoration:none;color:black}.head ul .dropdown:hover{border-bottom:3px solid black}.head ul .dropdown-content{display:none;position:absolute;background-color:white;min-width:120px;margin-top:3px;margin-right:-2.3rem;box-shadow:0px 8px 16px 0px rgba(0,0,0,0.2);z-index:1000}.head ul .dropdown-content a{color:black;padding:12px 16px;text-decoration:none;display:block;text-align:center}.head ul .dropdown-content a:hover{background-color:#f1f1f1}.head ul .dropdown:hover .dropdown-content{display:block}.head .customNav.opened{display:block}@media only screen and (min-width: 800px){.head .fb{display:none}.head .customNav{display:block}.head ul{list-style-type:none;margin:0;padding:0;text-align:center;font-family:'Lalezar'}.head ul li{margin-bottom:12px;display:inline-block;margin:0 1.2rem 0.4rem 1.5rem}}

.homeMainDiv{padding-top:0.5rem;font-family:'Lalezar'}.homeMainDiv section{margin:0 2rem}.homeMainDiv .firstBg .container{padding-top:6rem}.homeMainDiv .rightCol{text-align:center}.homeMainDiv .leftCol{display:block;margin:0 auto;text-align:center}.homeMainDiv .leftCol img{margin-top:2rem;width:100%}.homeMainDiv .column{flex:100% 1;max-width:100%}.homeMainDiv .column img{display:block;margin-top:1.5rem;width:100%}.homeMainDiv .tmBtn{background-color:#04abec;color:white;padding:10px 20px;text-align:center;text-decoration:none;display:inline-block;border-radius:50px;margin:1rem 0 1rem 0}.homeMainDiv #shpAroundBtn{margin-left:1rem}.homeMainDiv .storesSection{margin:5rem 0 0 0;text-align:center;padding-top:2rem;padding-bottom:5rem;background-color:rgba(255,228,196,0.356)}.homeMainDiv .storesSection .column{background-color:white;padding:0;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);margin:0 6px}.homeMainDiv .storesSection img{height:12rem;margin-top:0}@media only screen and (min-width: 800px){.homeMainDiv .container{margin:0 auto}.homeMainDiv .firstBg .container{display:flex;justify-content:space-between;align-items:center;padding-top:8.5rem}.homeMainDiv .leftCol{width:60%}.homeMainDiv .leftCol img{width:80%}.homeMainDiv .rightCol{display:block;width:40%;text-align:right;padding-right:6.2%}.homeMainDiv .rightCol h1{font-size:2.5rem;margin-bottom:0}.homeMainDiv .rightCol p{font-size:1.2rem}.homeMainDiv .randomClothes .container{display:flex;flex-wrap:wrap;justify-content:center;padding:0 10%;margin-top:3rem}.homeMainDiv .randomClothes .container .column{flex:25% 1;max-width:25%;padding:0 6px}.homeMainDiv .storesSection .container{display:flex;flex-wrap:wrap;justify-content:center;padding:0 10%;margin-top:3rem}.homeMainDiv .storesSection .container .column{flex:25% 1;max-width:25%}}

footer{background-color:black;color:white;padding:1rem;font-family:'Lalezar'}footer .container{display:flex;justify-content:space-around;text-align:center;flex-wrap:wrap;flex-direction:column;align-items:center}footer .container .column{margin-bottom:1em}@media only screen and (min-width: 800px){footer .container{flex-direction:row;margin:0 20%}}

