a{text-decoration:none;color:black}.commodityCard{background-color:white;padding:0;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);margin:1rem 0;text-align:center;padding-bottom:1rem;font-family:'Lalezar'}.commodityCard img{margin-top:0;vertical-align:middle;width:100%;height:23rem}@media only screen and (min-width: 800px){.commodityCard{flex:22% 1;max-width:22%;margin-right:2rem}}

.detailsMainDiv{padding-top:6rem;font-family:'Lalezar'}.detailsMainDiv .container{display:flex;padding-top:2rem;border-top:2px solid black;flex-wrap:wrap}.detailsMainDiv .container .right{flex:100% 1;width:100%;text-align:center}.detailsMainDiv .container .right img{width:90%;margin-top:1rem;max-height:75vh}.detailsMainDiv .container .left{flex:100% 1;width:100%;color:#72604a;text-align:center}.detailsMainDiv .container .left #name{font-size:3.2rem;margin-top:0}.detailsMainDiv .container .left ul{list-style-type:none;padding:0}.detailsMainDiv .container .left ul li{display:inline-block;margin:0 1rem;font-size:1.2rem;text-align:center;width:2rem;height:2rem;border:2px solid #6e5a40;border-radius:50%;cursor:pointer}.detailsMainDiv .container .left ul .selected{background-color:#6e5a40;color:white}.detailsMainDiv .container .left div{text-align:center;font-size:2rem;margin:5rem auto}.detailsMainDiv .container .left div #addBtn{background-color:#6e5a40;color:white;width:40%;margin:0 auto;font-size:1.2rem;padding:1rem}@media only screen and (min-width: 800px){.detailsMainDiv{padding-top:8rem}.detailsMainDiv .container .right{flex:50% 1;width:50%;text-align:center}.detailsMainDiv .container .right img{width:60%;max-height:75vh}.detailsMainDiv .container .left{flex:50% 1;width:50%;text-align:right}}

